import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { MessageService } from 'primeng/api';
import { BehaviorSubject, Observable, ReplaySubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MaService {


  private loadingSubject = new BehaviorSubject<boolean>(false);


  private permissions: string[] = [];

  constructor(
    private messageService: MessageService
  ) { }


  handleError(error: any) {
    this.messageService.add({
      severity: 'error',
      summary: 'Error',
      detail: error?.message || 'An unexpected error occurred.'
    });
  }

  showLoading() {
    this.loadingSubject.next(true);
  }
  hideLoading() {
    this.loadingSubject.next(false);
  }
  getLoading() {
    return this.loadingSubject.asObservable();
  }

  public setPermissions(perms: string[]) {
    this.permissions = perms;
  }

  public getPermissions() {
    return this.permissions;
  }

  hasPermission(p: string | string[]): boolean {
    const list = Array.isArray(p) ? p : [p];
    return list.some(x => this.permissions.includes(x));
  }


  modifyDateByUTC(date: Date, isGetMethod = true) {
    const localUTC = (new Date().getTimezoneOffset() / 60);
    date.setHours(date.getHours() + (!isGetMethod ? -localUTC : localUTC));
    return date;
  }

 

}
