import { Directive, Input, ViewChild } from '@angular/core';
import {
  ControlContainer,
  ControlValueAccessor,
  FormControl,
  FormControlDirective,
  UntypedFormControl,
} from '@angular/forms';

@Directive()
export abstract class BaseControlValueAccessor implements ControlValueAccessor {
  @ViewChild(FormControlDirective, { static: true })
  formControlDirective: FormControlDirective | undefined;
  @Input() formControl!: FormControl;
  @Input() formControlName!: string;

  constructor(protected controlContainer: ControlContainer) {}

  get control(): UntypedFormControl {
    return (
      this.formControl ||
      (this.controlContainer?.control?.get(
        this.formControlName
      ) as UntypedFormControl)
    );
  }

  registerOnChange(fn: any): void {
    if (this.formControlDirective?.valueAccessor) {
      this.formControlDirective.valueAccessor.registerOnChange(fn);
    }
  }

  registerOnTouched(fn: any): void {
    if (this.formControlDirective?.valueAccessor) {
      this.formControlDirective.valueAccessor.registerOnTouched(fn);
    }
  }

  writeValue(obj: any): void {
    if (this.formControlDirective?.valueAccessor) {
      this.formControlDirective.valueAccessor.writeValue(obj);
    }
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.formControlDirective?.valueAccessor?.setDisabledState) {
      this.formControlDirective.valueAccessor.setDisabledState(isDisabled);
    }
  }
}
