import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ButtonModule } from 'primeng/button';
import { DialogModule } from 'primeng/dialog';
import { CdkDragDrop, DragDropModule, moveItemInArray } from '@angular/cdk/drag-drop';
import { CheckboxModule } from 'primeng/checkbox';
import { FormsModule } from '@angular/forms';
import { Column } from '../table/table.interface';
import { TranslateService } from '@ngx-translate/core';
import { InputTextModule } from 'primeng/inputtext';
import { MaLibModule } from '../../ma-lib.module';
@Component({
  selector: 'app-config-display',
  standalone: true,
  imports: [ButtonModule, FormsModule, DialogModule, InputTextModule, CheckboxModule, MaLibModule, DragDropModule],
  templateUrl: './config.component.html',
  styleUrls: ['./config.component.scss']
})
export class ConfigDisplayComponent {
  @Input() cols!: Column[];

  visible: boolean = false;
  colsConfig: any[] = [];
  selectAll: boolean = false;
  filterText: string = '';

  @Output() onChangeConfig = new EventEmitter();

  constructor(
    private translate: TranslateService
  ) { }

  ngOnInit() {
    this.colsConfig = this.cols.map((col) => {
      return {
        ...col,
        checked: !col.hidden,
      };
    });
  }
  get filteredColsConfig() {
    return this.colsConfig.filter((col) =>
      col.header.toLowerCase().includes(this.filterText.toLowerCase())
    );
  }
  showDialog() {
    this.visible = true;
  }
  changeAll() {
    this.colsConfig.forEach((col) => {
      if (!col.fixed) {
        col.checked = this.selectAll;
      }
    });
    this.checkChangeAll();
  }
  checkChangeAll() {
    setTimeout(() => {
      const s1 = this.colsConfig.filter(col => col.checked).length;
      if (s1 === this.colsConfig.length) {
        this.selectAll = true;
      } else {
        this.selectAll = false;
      }
    }, 100);
  }
  drop(event: CdkDragDrop<string[]>) {
    this.checkChangeAll();
    if (this.colsConfig[event.currentIndex].fixed) {
      return;
    }
    moveItemInArray(this.colsConfig, event.previousIndex, event.currentIndex);
  }

  cancel() {
    this.visible = false;
  }
  apply() {
    this.visible = false;
    this.cols = this.colsConfig.map((col) => {
      return {
        ...col,
        hidden: !col.checked,
      };
    });
    this.onChangeConfig.emit(this.cols);
  }
  formatFilter() {
    const s1 = this.colsConfig.filter(col => col.checked).length;

    return s1.toString().padStart(2, '0') + '/' + this.colsConfig.length.toString().padStart(2, '0') + ' ' + this.translate.instant('selected');
  }
}
