import { HttpClient } from '@angular/common/http';
import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { SvgService } from './view-svg.service';

@Component({
  selector: 'view-svg',
  templateUrl: './view-svg.component.html',
  styles: [],
})
export class ViewSvgComponent implements OnChanges {
  @Input() name: string;

  svgContent: any;

  constructor(
    private svgService: SvgService,
    private sanitizer: DomSanitizer
  ) { }

  getSafeContent(): SafeHtml {
    return this.svgContent;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['name'] && this.name) {
      const iconName = changes['name'].currentValue;


      this.svgService.getSvg(iconName).subscribe(svgData => {
        this.svgContent = this.sanitizer.bypassSecurityTrustHtml(svgData);
      });
    }
  }
}