import { Directive, ElementRef, Input, OnInit, Renderer2 } from '@angular/core';
import { MaService } from '../ma-lib.service';

@Directive({
  selector: '[maPermission]'
})
export class MaPermissionDirective implements OnInit {
  @Input('maPermission') requiredPermissions: string | string[] = [];
  @Input() mode: 'disable' | 'hide' = 'disable';

  constructor(
    private el: ElementRef,
    private renderer: Renderer2,
    private permissionService: MaService
  ) {
  }

  ngOnInit() {
    const permissions = Array.isArray(this.requiredPermissions)
      ? this.requiredPermissions
      : [this.requiredPermissions];

    const hasPermission = permissions.some(p =>
      this.permissionService.hasPermission(p)
    );

    if (!hasPermission) {
      if (this.mode === 'disable') {
        this.renderer.setProperty(this.el.nativeElement, 'disabled', true);
        this.renderer.addClass(this.el.nativeElement, 'disabled');

      } else if (this.mode === 'hide') {
        this.renderer.setStyle(this.el.nativeElement, 'display', 'none');
      }
    }
  }
}