import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Optional,
  Output,
} from '@angular/core';
import { ControlContainer, FormControl, FormGroup, FormsModule, NG_VALUE_ACCESSOR, ReactiveFormsModule } from '@angular/forms';
import { BaseControlValueAccessor } from '../base-control-value-accessor';
import { OverlayPanelModule } from 'primeng/overlaypanel';
import { DropdownModule } from 'primeng/dropdown';
import { MaLibModule } from '../../../ma-lib.module';

@Component({
  selector: 'dropdown',
  templateUrl: './dropdown.component.html',
  standalone: true,
  imports: [
    MaLibModule,
    ReactiveFormsModule,
    FormsModule,
    OverlayPanelModule,
    DropdownModule
  ],
  styleUrls: ['dropdown.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DropdownComponent),
      multi: true,
    },
  ],
})
export class DropdownComponent extends BaseControlValueAccessor implements OnInit {

  @Input() label: string = '';
  @Input() options: any[] = [];
  @Input() placeholder: string = '';
  @Input() optionLabel: string = 'label';
  isOpen: boolean = false;
  constructor(
    @Optional() public override controlContainer: ControlContainer,
    private cdr: ChangeDetectorRef
  ) {
    super(controlContainer);
  }

  get formGroup(): FormGroup {
    return this.controlContainer?.control as FormGroup;
  }

  ngOnInit(): void {
    this.cdr.detectChanges();
  }

  handleDropdownOpen(): void {
    this.isOpen = true;
  }
  handleDropdownClose(): void {
    this.isOpen = false;
  }
}
