import { CommonModule } from '@angular/common';
import { Component, ContentChild, EventEmitter, Input, OnDestroy, Output, SimpleChanges, TemplateRef } from '@angular/core';
import { ButtonModule } from 'primeng/button';
import { TableModule } from 'primeng/table';
import { PaginatorModule, PaginatorState } from 'primeng/paginator';
import { DropdownModule } from 'primeng/dropdown';
import { FormsModule } from '@angular/forms';
import { debounceTime, Observable, Subject, takeUntil } from 'rxjs';
import { Column, ISearchListInput, ISearchListOutput } from './table.interface';
import { SkeletonModule } from 'primeng/skeleton';
import { TooltipModule } from 'primeng/tooltip';
import { MaService } from '../../ma-lib.service';
import { MaLibModule } from '../../ma-lib.module';

@Component({
  selector: 'app-table',
  standalone: true,
  imports: [TableModule, FormsModule, TooltipModule, SkeletonModule, DropdownModule, ButtonModule, PaginatorModule, CommonModule, MaLibModule],
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent implements OnDestroy {
  @Input() datas!: any[];
  @Input() havePaginator = true;
  @Input() cols!: Column[];
  @Input() autoSearchAfterViewInit = true;
  @Input() selectionMode: 'single' | 'multiple' | null = 'single';
  @Input() inputSearch: any;
  @Input() scrollHeight = 'calc(100vh - 382px)';
  @Input() getList$: (input: ISearchListInput) => Observable<any>;
  @Input() placeFooter: 'bottom' | 'top' = 'bottom';
  @Output() onRowSelected = new EventEmitter<any>();

  selectedRow: any;
  offset: number = 0;
  limit: number = 20;
  currentPage: number = 0;
  totalRecords: number = 0;
  optionPaginators = [
    { label: '20', value: 20 },
    { label: '50', value: 50 },
    { label: '100', value: 100 },
    { label: '200', value: 200 }
  ];

  private searchTrigger$ = new Subject<void>();
  private destroy$ = new Subject<void>();
  private isTriggeringSearch = false;
  isLoading: boolean = false;
  totalWidth: number = 0;
  @ContentChild('templateCell', { static: false }) templateCellTemplate!: TemplateRef<any>;

  constructor(
    private maService: MaService
  ) { }

  ngOnInit() {
    this.calculateColumnWidths();

    this.searchTrigger$.pipe(
      debounceTime(300),
      takeUntil(this.destroy$)
    ).subscribe(() => {
      this.executeSearch();
    });

    if (this.autoSearchAfterViewInit) {
      this.triggerSearch();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['cols']) {
      this.calculateColumnWidths();
    }
    if (changes['inputSearch'] && !changes['inputSearch'].firstChange) {
      this.offset = 0;
      this.triggerSearch();
    }
  }

  onPageChange(event: PaginatorState): void {
    this.offset = event.first;
    this.limit = event.rows;
    this.currentPage = event.page;
    this.triggerSearch();
  }

  onRowsChange(): void {
    this.offset = 0;
    this.triggerSearch();
  }

  private triggerSearch(): void {
    if (this.isTriggeringSearch) {
      return;
    }

    this.isTriggeringSearch = true;
    this.searchTrigger$.next();

    setTimeout(() => {
      this.isTriggeringSearch = false;
    }, 200);
  }

  private executeSearch(): void {
    if (this.isLoading) {
      return;
    }
    if (!this.autoSearchAfterViewInit && (!this.inputSearch || Object.keys(this.inputSearch).length === 0)) {
      return;
    }
    let inputSearch
    if (this.inputSearch) {
      inputSearch = JSON.parse(JSON.stringify(this.inputSearch));
    } else {
      inputSearch = {};
    }
    const searchParams = { ...inputSearch, offset: this.offset, page: this.currentPage, size: this.limit, limit: this.limit };

    for (const key in searchParams) {
      if (searchParams[key] instanceof Date) {
        searchParams[key] = this.maService.modifyDateByUTC(searchParams[key]).toISOString();
      }
      if (key == 'dateRange' && searchParams[key].length == 2) {
        if (searchParams[key][0]) {
          searchParams[key][0] = this.maService.modifyDateByUTC(new Date(searchParams[key][0])).toISOString();
        }
        if (searchParams[key][1]) {
          searchParams[key][1] = this.maService.modifyDateByUTC(new Date(searchParams[key][1])).toISOString();
        }
      }
    }
    if (this.getList$) {
      this.datas = [];
      this.totalRecords = 0;
      const tableWrapper = document.querySelector('.p-datatable-wrapper');
      if (tableWrapper) {
        tableWrapper.scrollTo({ top: 0 });
      }
      this.isLoading = true;
      this.getList$(searchParams).subscribe({
        next: (result) => {
          this.isLoading = false;
          this.datas = result.items;
          this.totalRecords = result.total;
        },
        error: (err) => {
          this.isLoading = false;
        }
      });
    }
  }

  onRowSelect(event: any): void {
    this.onRowSelected.emit(this.selectedRow);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  calculateColumnWidths(): void {
    let totalWidth = 0;

    this.cols.forEach(col => {
      if (!col.hidden) {
        if (!col.width) {
          col.width = '100px';
        }
        const widthValue = parseInt(col.width.replace('px', ''), 10);
        totalWidth += widthValue;
      }
    });

    this.totalWidth = totalWidth;
    this.cols.forEach(col => {
      if (!col.hidden) {
        const widthValue = parseInt(col.width.replace('px', ''), 10);
        col.percentWidth = ((widthValue / totalWidth) * 100).toFixed(2) + '%';
      }
    });
  }

}