import { CommonModule } from '@angular/common';
import { Component, forwardRef, Input, OnInit, Optional } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { CardModule } from 'primeng/card';
import { TooltipModule } from 'primeng/tooltip';
import { MaLibModule } from '../../../ma-lib.module';
import { ControlContainer, FormGroup, NG_VALUE_ACCESSOR } from '@angular/forms';
import { BaseControlValueAccessor } from '../base-control-value-accessor';
import { ControlComponent } from '../control/control.component';
import { CardForm } from './card-form.interface';
@Component({
  selector: 'app-card-form',
  standalone: true,
  imports: [CardModule, CommonModule, TooltipModule, MaLibModule, ControlComponent, TranslateModule],
  templateUrl: './card-form.component.html',
  styleUrls: ['./card-form.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CardFormComponent),
      multi: true,
    },
  ],
})
export class CardFormComponent extends BaseControlValueAccessor implements OnInit {
  @Input() header!: string

  @Input() data!: any;

  @Input() cols!: CardForm[];

  @Input() formGroup!: FormGroup;

  get f() {
    return this.formGroup?.controls;
  }


  constructor(@Optional() public override controlContainer: ControlContainer) {
    super(controlContainer);
  }
  ngOnInit() {
  }
}
