import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Optional,
  Output,
} from '@angular/core';
import { ControlContainer, FormGroup, NG_VALUE_ACCESSOR, ReactiveFormsModule } from '@angular/forms';
import { BaseControlValueAccessor } from '../base-control-value-accessor';
import { InputTextModule } from 'primeng/inputtext';
import { MaLibModule } from '../../../ma-lib.module';
import { DropdownModule } from 'primeng/dropdown';
import { CalendarModule } from 'primeng/calendar';
import { ErrorMessageComponent } from '../error-message/error-message.component';


type inputType =
  | 'text'
  | 'number'
  | 'password'
  | 'select'
  | 'multi-select'
  | 'checkbox'
  | 'radio'
  | 'radio-group'
  | 'textarea'
  | 'date'
  | 'date-range'
  | 'date-range-time'
  | 'time'
  | 'tree-select'

@Component({
  selector: 'app-control',
  templateUrl: './control.component.html',
  standalone: true,
  imports: [
    MaLibModule,
    ReactiveFormsModule,
    InputTextModule,
    CalendarModule,
    DropdownModule,
    ErrorMessageComponent
  ],
  styleUrls: ['control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ControlComponent),
      multi: true,
    },
  ],
})
export class ControlComponent extends BaseControlValueAccessor implements OnInit {
  @Input() type: inputType = 'text';

  @Input() label = '';
  @Input() labelClass = '';
  @Input() placeholder = '';
  @Input() isRequired: boolean = false;
  @Input() allowClear: boolean = true;
  @Input() labelSpan = 4;

  @Input() selectLabel = 'label';
  @Input() selectValue = 'value';
  @Input() options: any[] = [];


  @Input() isShowTime = false;
  @Input() formatDate = 'dd/mm/yy';


  @Input() minDate: Date = new Date('1900/01/01');
  @Input() maxDate!: Date;


  @Input() maxlength: number = 0;
  @Input() maxNumber: number;
  @Input() minNumber: number;




  now: Date;
  numNight = 1;
  selectedOption: any;

  constructor(@Optional() public override controlContainer: ControlContainer) {
    super(controlContainer);
  }

  get formGroup(): FormGroup {
    return this.controlContainer?.control as FormGroup;
  }

  ngOnInit(): void {
    if (this.maxlength == 0) {
      if (this.type === 'text') {
        this.maxlength = 255;

      } else if (this.type === 'number') {
        this.maxlength = 14;
      } else if (this.type === 'textarea') {
        this.maxlength = 500;
      }
    }

  }


  onInputNumberSliceMaxLength(event: any): void {
    if (this.maxlength < 1) {
      return;
    }
    const input = event.target;
    const value = input.value;
    if (value.length > this.maxlength) {
      input.value = value.slice(0, this.maxlength);
      this.control.setValue(input.value === '' || input.value === null || input.value === undefined || Number.isNaN(input.value) ? null : Number(input.value));
    }
  }

  checkDate() {
    setTimeout(() => {
      const container = document.querySelector('.p-datepicker-group-container');
      if (!container) return;
      const childDivs = container.querySelectorAll('.range-date-first')
      const childDiv2s = container.querySelectorAll('.range-date-last')
      childDivs.forEach(div => {
        div.classList.remove('range-date-first', 'range-date-last');
      });
      childDiv2s.forEach(div => {
        div.classList.remove('range-date-first', 'range-date-last');
      });
      if (this.control.value && (this.control.value.length > 0 || this.type === 'date')) {
        const labels = container.querySelectorAll('span.p-highlight:not(.p-disabled)');
        if (labels.length > 0) {
          labels[0].parentElement.classList.add('range-date-first');
          labels[labels.length - 1].parentElement.classList.add('range-date-last');
        }
      }
    }, 50);

  }

}
