import { Inject, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, ReplaySubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SvgService {
  private cache: Map<string, ReplaySubject<string>> = new Map();

  constructor(
    private http: HttpClient,
    @Inject('environment') private environment: any) { }

  getSvg(iconName: string): Observable<string> {
    if (this.cache.has(iconName)) {
      return this.cache.get(iconName)!.asObservable();
    } else {
      const subject = new ReplaySubject<string>(1);
      this.cache.set(iconName, subject);
      const path = `${this.environment.assets}/svg/${iconName}.svg`;

      this.http.get(path, { responseType: 'text' }).subscribe(
        (data) => {
          subject.next(data);
          subject.complete();
        },
        (err) => {
          subject.error(err);
        }
      );
      return subject.asObservable();
    }
  }
}